import React, { Component } from 'react';
export default class header extends Component {
	render() {
		return (
			<header id="home">
				<nav id="nav-wrap">
					<a className="mobile-btn" href="#nav-wrap" title="Show navigation">
						Show navigation
					</a>
					<a className="mobile-btn" href="#" title="Hide navigation">
						Hide navigation
					</a>
					<ul id="nav" className="nav">
						<li className="current">
							<a className="smoothscroll" href="#home">
								Inicio
							</a>
						</li>
						<li>
							<a className="smoothscroll" href="#about">
								Acerca de mí
							</a>
						</li>
						<li>
							<a className="smoothscroll" href="#resume">
								Resumen
							</a>
						</li>
						<li>
							<a className="smoothscroll" href="#portfolio">
								Portafolio
							</a>
						</li>
						<li>
							<a className="smoothscroll" href="#testimonials">
								Testimonials
							</a>
						</li>
						<li>
							<a className="smoothscroll" href="#contact">
								Contacto
							</a>
						</li>
					</ul>{' '}
					{/* end #nav */}
				</nav>{' '}
				{/* end #nav-wrap */}
				<div className="row banner">
					<div className="banner-text">
						<h1 className="responsive-headline">Soy Cesar Morelos Damaso.</h1>
						<h3>
							Soy egresado de la <span>Universidad Autónoma del Estado de México</span>, de
							la carrera de <span>Ingeniería en Computación</span>. He tenido la oportunidad
							de trabajar como soporte informático y desarrollador de aplicaciones web en{' '}
							<span>JavaScript</span>. Saber mas{' '}
							<a className="smoothscroll" href="#about">
								sobre mí
							</a>
							.
						</h3>
						<hr />
						<ul className="social">
							<li>
								<a href="#">
									<i className="fa fa-facebook" />
								</a>
							</li>
							<li>
								<a href="#">
									<i className="fa fa-twitter" />
								</a>
							</li>
							<li>
								<a href="#">
									<i className="fa fa-google-plus" />
								</a>
							</li>
							<li>
								<a href="#">
									<i className="fa fa-linkedin" />
								</a>
							</li>
							<li>
								<a href="#">
									<i className="fa fa-instagram" />
								</a>
							</li>
							<li>
								<a href="#">
									<i className="fa fa-dribbble" />
								</a>
							</li>
							<li>
								<a href="#">
									<i className="fa fa-skype" />
								</a>
							</li>
						</ul>
					</div>
				</div>
				<p className="scrolldown">
					<a className="smoothscroll" href="#about">
						<i className="icon-down-circle" />
					</a>
				</p>
			</header>
		);
	}
}
